header {
	position:fixed;
	top:50%;
	transform:translateY(-50%);
	z-index:4;
	left:10px;
	.vertical_menu_container{
	  	margin: 0 auto;
		ul {
			
			li {
				height:40px;
				list-style:none;
				text-transform:uppercase;
				div {
					display:block;
					transform:rotate(45deg);
					width:18px;
					height:18px;
					border:1px solid red;
					margin-top:6px;
					&.menu_blue {
						border:1px solid blue;
						&.menuIsActive {
							background:blue;
						}
					}
				}
				.menu_item {
					text-transform:uppercase;
					display:none;
					line-height:1.5;
					font-size:14px;
					color:blue;
					margin: -18px 0 0 30px;
				}
				&:hover {
					.menu_item {
						display:block;
					}
				}
			}
		
		}
	}
}

.menuIsActive {
	background: red;
}